  stroke-width: 1.5;
}
.progress-bar__progress {
  fill: none;
  stroke: #ffc464;
  stroke-dasharray: 100;
  stroke-dashoffset: -100;
  stroke-linecap: round;
  stroke-width: 1.5;
  transition: stroke-dashoffset 1.5s ease-in-out;
  transform-origin: center;
  transform-box: fill-box;
}

